.preview{
    width: 100%;
    margin-top: 35px;
    background: url('../../img/preview.png') center no-repeat;
    background-size: cover;
    aspect-ratio: 1440 / 698;
    color: white;      
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 16%;
}

.preview main{
    align-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 27px;
}

.preview footer{
    align-self: start;
    padding: 15px 30px;
    margin:  40px 20px 3px 20px;
    background-color: #050505;
    border-radius: 1px 1px 0 0;

    font-family: Palatino Linotype;
    font-size: 15px;
}

.preview button{
    color: #fff;
    text-align: center;
    font-family: Cardo;
    font-size: 20px;
    font-weight: 700;
    line-height: 20px; 
    padding: 5px 40px;

    border: 1px solid white;
    background-color: rgba(0, 0, 0, 0);
}